import React from "react";
//import * as style from "./style.css"

import * as Math2 from "../../../../Other/math2"
import Stars from "../../../../Drawing/stars";
import DrawnPlanet from "../../../../Drawing/planet";
import Planet from "./Planet"
import Image from "../../../Image";

export default class SolarSystem extends React.Component{

    constructor(props){
        super(props);

        this.state = {

        }
    }

    componentDidMount(){
        if(!this.state.stars) 
            this.drawStars();
        
        if(!this.state.planets){
            //this.drawPlanets()
        }
        
    }

    async drawStars(){
        const space = new Stars(window.innerWidth * 2, window.innerHeight * 2);
        const image = space.draw();
        this.setState({stars: image});
    }

    async drawPlanets(){
        const amount = Math.floor(Math2.random(5, 10));
        let planets = [];
        for(let i = 0; i < amount; i++)
            planets[i] = this.drawPlanet();
        this.setState({planets})
    }

    drawPlanet(){
        const planet = new DrawnPlanet()
        return planet.draw();
    }

    render(){
        return (
        <div style={{width:"100%", height:"100%"}}>
            <Image style={{width:"100%", height:"100%"}} image={this.state.stars} title="Space and Stars"/>
            {this.state?.planets?.map((data, index) => 
                <Planet key={index} planet={data} 
                    x={Math2.random(100, window.innerWidth * 0.9)}
                    y={Math2.random(100, window.innerHeight * 0.9)}
                />
            )} 
            
        </div>
        )
    }
}