import React from "react";
import CardFlip from "../../../CardFlip"
import "./style.css"
import placeholder from "./placeholder.png"

export default class Projects extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            info: this.verifyInfo(props.info),
        }
    }

    verifyInfo(info){
        info.title = info.title || "Unnamed";
        info.image = info.image || placeholder;
        return info;
    }

    renderAsCard(){
        const info = this.state.info;
        return (
        <CardFlip>
            <div className="projectContainer">
                <image src={info.image} alt={info.title + "_Image"} />
            </div>
            <div className="projectContainer">
                <span className="cText">{info.title}</span>
                <p className="cText">{info.description}</p>
                <a className="cText" href={info.link.ref}>{info.link.name || "Link"}</a>
            </div>
            <div className="projectContainer">
                <span className="cText">Using: </span>
                <span className="cText">
                    {info.tech.join(', ')}
                </span>    
            </div>
        </CardFlip>
        )
    }

    renderAsDiv(info){
        return (
            <div className="projectContainer">
                <image src={info.image} alt={info.title + "_Image"} />
                <span className="cText">{info.title}</span>
                <p className="cText">{info.description}</p>
                <a className="cText" href={info.link.ref}>{info.link.name || "Link"}</a>
                <span className="cText">Using: </span>
                <span className="cText">
                    {info.tech.join(', ')}
                </span>    
            </div>
        )
    }

    render(){
        const info = this.state.info;
        return this.renderAsDiv(info);
    }
}