import React from 'react'
import TerminalBackground from "../../../../../Drawing/terminalbk";
import * as Math2 from "../../../../../Other/math2"
import ScreenBar from './screenbar';

function getTerminalBk(){
    try{
        const size = Math.max(window.innerHeight, window.innerWidth)
        const terminalbk = new TerminalBackground(size, size);
        const img = terminalbk.draw();
        return img;
    }catch(e){
        console.error(e);
    }
}

const terminalbk = getTerminalBk()

export default class OldScreen extends React.Component{

    components = {};

    constructor(props){
        super(props);

        this.state = {
            
        }
    }

    componentDidMount() {
        setTimeout(() => this.moveScreen(), 1000)
    }

    moveScreen(){
        let top = Math2.random(0, -100);
        while(top === this.state.top)
            top = Math2.random(0, -100);
        this.setState({top})
    }

    imageStyle(){
        const top = this.state.top || Math2.random(0, -100);
        return { top: `${top}%` }
    }

    render(){
        return (
            <div className="tabScreenContainer">
                <img className="tabScreenStyle" style={this.imageStyle()}
                    onTransitionEnd={() => this.moveScreen()}
                    alt="screen" src={terminalbk} />
                <ScreenBar />
            </div>
        )
    }
}