import React from 'react'

import TerminalBackground from "../../../../../Drawing/terminalbk";

function getTerminalBk(){
    try{
        const terminalbk = new TerminalBackground(window.innerWidth, 60);
        const img = terminalbk.drawLine();
        return img;
    }catch(e){
        console.error(e);
    }
}

const terminalbk = getTerminalBk()

export default class ScreenBar extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            running: false,
        }
    }

    componentDidMount(){
        setTimeout(() => this.run(), 1000);
    }

    run(){
        this.setState({running:!this.state.running})
    }

    barStyle(){
        const run = this.state.running;
        return {
            position:"absolute",
            width:"100%",
            visibility: run ? "visible" : "hidden",
            top: run ? "150%" : -60,
            left:0,
            transition: `top 5s linear, visibility 0s linear`
        }
    }

    render(){
        return (
            <img src={terminalbk} style={this.barStyle()} alt="line"
                onTransitionEnd={() => this.run()}
            />
        )
    }
}