import Canvas from "./canvas"
import Color from "color"

export default class TerminalBackground extends Canvas {

    draw(){
        const spacing = 5;
        
        this.context.lineWidth = 3;
        this.context.strokeStyle = Color("green").lighten(0.5).fade(0.5).opaquer(-0.6);

        this.context.fillStyle = Color("green").lighten(0.5).desaturate(1).opaquer(-0.5);
        this.context.beginPath();
        this.context.fillRect(0, 0, this.size.x, this.size.y)

        for(let y = 0; y < this.size.y; y += spacing){
            this.context.beginPath()
            this.context.moveTo(0, y);
            this.context.lineTo(this.size.x, y);
            this.context.stroke();
            this.context.closePath();
        }

        return super.draw();
    }

    drawLine(){
        let color = Color("green").lighten(0.5).fade(0.4);
        const width = 5;
        const mid = this.size.y / 2;
        this.context.lineWidth = width;
        const xEnd = this.size.x;

        for(let y = 0; y < this.size.y; y++){
            const fromMid = (Math.abs(y - mid) / this.size.y) * 2;
            this.context.strokeStyle = color.opaquer(-fromMid);
            
            this.context.beginPath();

            this.context.moveTo(0, (y * width));
            this.context.lineTo(xEnd, (y * width));

            this.context.stroke();
            this.context.closePath();
        }

        return super.draw();
    }
}